// pages/my-posts/[userSignId].tsx

import { useRouter } from 'next/router';
import { useEffect, useState } from 'react';
import { getMyPosts } from '@/api/blogService/blog'; // 1번에서 정의한 함수
import { PostEntity } from '@/types/blogService/blogType'; // PostEntity 타입을 사용
import styles from '@/styles/blogService/MyPosts.module.css'; // 새로 만들 스타일 파일

// 게시글 미리보기 컴포넌트 (재사용 가능하도록 별도로 정의)
const PostPreview: React.FC<{ post: PostEntity }> = ({ post }) => {
  const router = useRouter();
  const handlePostClick = () => {
    // 실제 게시글 상세 페이지 경로로 이동
    router.push(`/post/${post.postId}`);
  };

  // createdAt을 한국식 날짜 형식으로 변환하는 유틸리티
  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('ko-KR');
  };

  return (
      <div className={styles.postPreview} onClick={handlePostClick}>
        {/* 썸네일 이미지가 있다면 표시 */}
        {post.thumbnail && (
            <img
                src={post.thumbnail}
                alt={post.title}
                className={styles.thumbnail}
            />
        )}
        <div className={styles.postContent}>
          <h3 className={styles.postTitle}>{post.title}</h3>
          <p className={styles.postSnippet}>{post.content.substring(0, 100)}...</p>
          <div className={styles.postMeta}>
            <span>작성일: {formatDate(post.createdAt)}</span>
            <span>조회수: {post.viewCount}</span>
          </div>
        </div>
      </div>
  );
};

export default function MyPostsPage() {
  const router = useRouter();
  const { userSignId } = router.query;

  const [posts, setPosts] = useState<PostEntity[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [currentPage, setCurrentPage] = useState(0);
  const [isLastPage, setIsLastPage] = useState(false);

  const authorId = Array.isArray(userSignId) ? userSignId[0] : userSignId;
  const currentUserId = typeof window !== "undefined" ? localStorage.getItem("userSignId") : null;

  useEffect(() => {
    if (!authorId || !router.isReady) {
      return;
    }

    const fetchPosts = async (page: number) => {
      if (page === 0) setIsLoading(true);
      setError(null);

      try {
        // API 호출
        const response = await getMyPosts(authorId,page);

        // 페이지 0이면 목록을 새로 설정, 아니면 기존 목록에 추가 (무한 스크롤 구현 시)
        setPosts(prev => page === 0 ? response.content : [...prev, ...response.content]);

        setIsLastPage(response.last);
      } catch (err) {
        setError("게시글 목록을 불러오는 데 실패했습니다.");
        console.error(err);
      } finally {
        setIsLoading(false);
      }
    };

    fetchPosts(currentPage);
  }, [authorId, router.isReady, currentPage]);

  // 무한 스크롤 또는 "더 보기" 버튼 클릭 시 다음 페이지 로드
  const handleLoadMore = () => {
    if (!isLastPage && !isLoading) {
      setCurrentPage(prev => prev + 1);
    }
  };

  // userSignId가 없는 경우 (초기 로딩 단계)
  if (!authorId) {
    return <div className={styles.loading}>정보를 불러오는 중...</div>;
  }

  // 로딩 중
  if (isLoading && currentPage === 0) {
    return <div className={styles.loading}>게시글 목록 로딩 중...</div>;
  }

  // 에러 발생
  if (error) {
    return <div className={styles.error}>{error}</div>;
  }

  // 게시글 없음
  if (posts.length === 0) {
    return (
        <div className={styles.container}>
          <h1 className={styles.pageTitle}>{authorId}님의 게시물</h1>
          <p className={styles.noContent}>작성된 게시글이 없습니다.</p>
        </div>
    );
  }

  // 정상 렌더링
  return (

      <div className={styles.container}>
        <h1 className={styles.pageTitle}>
          {authorId}님의 게시물 ({posts.length}개)
        </h1>

        <div className={styles.postList}>
          {posts.map(post => (
              <PostPreview key={post.postId} post={post} />
          ))}
        </div>

        {/* 더 보기/무한 스크롤 버튼 */}
        {!isLastPage && (
            <button
                onClick={handleLoadMore}
                disabled={isLoading}
                className={styles.loadMoreButton}
            >
              {isLoading ? '로딩 중...' : '더 보기'}
            </button>
        )}
      </div>
  );
}